<template>
  <div>
    <topMember v-on:member="getInfor" :index="0"></topMember>
    <div class="box mar">
      <div class="over">
        <div style="float:left;width:480px;">
          <div class="userTop fffbg">
            <img src="../../static/renwu.png">
            <div>
              <div class="over">
                <span class="pull-left">
                  {{member.account}}，{{welcomeWord[member.vip]}}欢迎您！当前余额：
                  <span class="red">￥{{mytotal/100}}</span>
                </span>
                <el-button
                  class="pull-right"
                  size="mini"
                  type="primary"
                  @click="$router.push({path:'/recharge'})"
                >充值</el-button>
              </div>
              <div class="fontBt">
                <el-button @click="editInforDiv=true" type="text">编辑资料</el-button>|
                <el-button @click="$router.push({path:'/cost'})" type="text">物流成本</el-button>|
                <el-button @click="capitalDetail" type="text">资金明细</el-button>|
                <el-button @click="editMmDiv=true" type="text">修改密码</el-button>
              </div>
            </div>
          </div>
          <el-row class="mar" :gutter="20">
            <el-col :span="12">
              <div class="fffbg ptkd">
                <div class="mainH">
                  <div class="mainHICO"></div>快递价格
                </div>
                <div class="pad15">
                  <div class="over">
                    <span class="pull-left">区域</span>
                    <span class="pull-right">普通地区</span>
                  </div>
                  <div class="over">
                    <span class="pull-left">首重0.5kg内</span>
                    <span class="pull-right">{{priceForm.nearPrice1/100}}元/单</span>
                  </div>
                  <div class="over">
                    <span class="pull-left">0.5-1kg</span>
                    <span class="pull-right">{{priceForm.nearPrice2/100}}元/单</span>
                  </div>
                  <div class="over">
                    <span class="pull-left">1kg续重</span>
                    <span class="pull-right">{{priceForm.nearPrice3/100}}元/kg</span>
                  </div>
                </div>
              </div>
            </el-col>
            <el-col :span="12">
              <div class="fffbg ptkd">
                <div class="mainH">
                  <div class="mainHICO"></div>服务指南
                </div>
                <div class="pad15">
                  <div class="over">
                    <img style="float:left;" src="../../static/kefu.png">
                    <div style="width:140px;float:right;">
                      <div class="fwzn">专属运营：{{memberInfor.contactPerson}}</div>
                      <div class="fwzn2">
                        微信:{{memberInfor.wx}}
                        <br>
                        Q Q:{{memberInfor.qq}}
                      </div>
                    </div>
                  </div>
                  <el-row class="mar" :gutter="15">
                    <el-col :span="12">
                      <el-button size="small" style="width:100%" type="primary">帮助中心</el-button>
                    </el-col>
                    <el-col :span="12">
                      <el-button size="small" style="width:100%" type="primary">视频介绍</el-button>
                    </el-col>
                  </el-row>
                </div>
              </div>
            </el-col>
          </el-row>
        </div>
        <div style="float:right;width:700px;">
          <img src="../../static/memberBanner.png">
        </div>
      </div>
      <div class="mar fffbg">
        <div class="mainH pad">
          <div class="mainHICO"></div>
          <span class="pull-left">采购任务</span>
          <el-button
            size="small"
            class="pull-right"
            @click="$router.push({path:'/buy'})"
            type="primary"
          >立即采购</el-button>
        </div>
        <div class="pad15">
          <el-table
            :data="tableData"
            border=""
            :header-cell-style="{background:'#f7f7f7'}"
            style="width: 100%"
          >
            <el-table-column prop="id" label="发货任务编号" align="center" width="200"></el-table-column>
            <el-table-column prop="orderCount" align="center" label="订单数"></el-table-column>
            <el-table-column align="center" label="支付金额">
              <template slot-scope="scope">{{scope.row.money/100}} 元</template>
            </el-table-column>
            <el-table-column align="center" label="支付时间">
              <template slot-scope="scope">{{scope.row.createTime | timeFormater}}</template>
            </el-table-column>
            <el-table-column prop="state" align="center" label="任务状态">
              <template slot-scope="scope">
                <span v-if="scope.row.state==0">未发货</span>
                <span v-if="scope.row.state==1">已发货</span>
              </template>
            </el-table-column>
            <el-table-column fixed="right" label="操作" align="center" width="100">
              <template slot-scope="scope">
                <!-- <el-link type="primary" :underline="false">默认支付</el-link>
                <el-link type="primary" :underline="false">撤销</el-link>-->
                <el-link type="primary" @click="rejectRow(scope.row)">退货</el-link>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="pageDiv">
          <el-pagination
            background=""
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :page-sizes="[10, 20, 30, 40]"
            :page-size="size"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total"
          ></el-pagination>
        </div>
      </div>
    </div>
    <foot></foot>
    <el-dialog title="编辑资料" :visible.sync="editInforDiv" width="30%">
      <el-form label-width="80px">
        <el-form-item label="QQ">
          <el-input v-model="member.qq"></el-input>
        </el-form-item>
        <el-form-item label="微信">
          <el-input v-model="member.wx"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editInforDiv = false">取 消</el-button>
        <el-button type="primary" @click="editInforDiv = false;editInfor()">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog title="修改密码" :visible.sync="editMmDiv" width="30%">
      <el-form label-width="120px">
        <!-- <el-form-item label="手机号">
    <el-input></el-input>
  </el-form-item>
  <el-form-item label="验证码">
     <el-input style="width:50%"></el-input>
     <el-button type="primary">获取验证码</el-button>
        </el-form-item>-->
        <el-form-item label="旧密码">
          <el-input v-model="oldPassword" show-password></el-input>
        </el-form-item>
        <el-form-item label="新密码">
          <el-input v-model="password" show-password></el-input>
        </el-form-item>
      </el-form>
      <div class="mar cen">
        <el-button @click="editMmDiv = false">取 消</el-button>
        <el-button type="primary" @click="editMmDiv = false;editPassword()">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="提示" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
      <span>该操作将删除该任务并退还用户消费的费用，您是否确认退货</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogSure">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import foot from "./public/foot.vue";
import topMember from "./public/topMember.vue";
import { hex_md5 } from "../utils/md5";
import back from "../mixins/back";
import timeFormater from "@/utils/timeFormate";
export default {
  components: {
    foot,
    topMember
  },
  mixins: [back],
  filters: {
    timeFormater
  },
  data() {
    return {
      priceForm: "",
      mytotal: 0,
      member: "",
      password: "",
      oldPassword: "",
      tableData: [],
      page: 1,
      size: 10,
      total: 0,
      editInforDiv: false,
      editMmDiv: false,
      memberInfor: {},
      welcomeWord:['','尊敬的VIP','尊敬的超级VIP'],

    };
  },
  mounted() {
    if (sessionStorage.getItem("token") == null) {
      this.$router.push({
        path: "/login"
      });
    }
    this.getPrice();
    this.taskList();
    this.getMoney();
    this.getMember();
  },
  methods: {
    getInfor(data) {
      this.member = data;
    },
    getMember() {
      let that = this;
      this.axios
        .get("/giftmall/webSite/info", {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
            domain: this.url,

            Authorization: sessionStorage.getItem("token")
          }
        })
        .then(function(response) {
          if (response.data.code == 200) {
            that.memberInfor = response.data.data;
            sessionStorage.setItem(
              "memberInfor",
              JSON.stringify(that.memberInfor)
            );
          }
        });
    },
    editInfor() {
      let that = this;
      // this.axios.put(process.env.API_HOST+'user',
      this.axios
        .put(
          "/giftmall/user",
          this.qs.stringify({ qq: this.member.qq, wx: this.member.wx }),
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              // 'domain':'taobao1.tianyalei.com',
              domain: this.url,
              Authorization: sessionStorage.getItem("token")
            }
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
            that.$message({
              message: "修改成功",
              type: "success"
            });
          } else {
            that.$message({
              message: response.data.message,
              type: "warning"
            });
          }
        });
    },
    editPassword() {
      let that = this;
      // this.axios.put(process.env.API_HOST+'user/password',
      this.axios
        .put(
          "/giftmall/user/password",
          this.qs.stringify({
            oldPassword: hex_md5(this.oldPassword),
            password: hex_md5(this.password)
          }),
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              // 'domain':'taobao1.tianyalei.com',
              domain: this.url,
              Authorization: sessionStorage.getItem("token")
            }
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
            that.$message({
              message: "修改成功",
              type: "success"
            });
            that.$router.push({
              path: "/login"
            });
          } else {
            that.$message({
              message: response.data.message,
              type: "warning"
            });
          }
        });
    },
    getMoney() {
      let that = this;
      this.axios
        .get(
          // process.env.API_HOST+"wallet/myTotal",
          "/giftmall/wallet/myTotal",
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              // "domain": "taobao1.tianyalei.com",
              domain: this.url,

              Authorization: sessionStorage.getItem("token")
            }
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
            that.mytotal = response.data.data;
          }
        });
    },
    taskList() {
      let that = this;
      this.axios
        .get(
          // process.env.API_HOST+"task?&page="+(that.page-1)+'&size='+that.size,
          "/giftmall/task?&page=" + (that.page - 1) + "&size=" + that.size,
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              // "domain": "taobao1.tianyalei.com",
              domain: this.url,
              Authorization: sessionStorage.getItem("token")
            }
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
            for (var i = 0; i < response.data.data.list.length; i++) {
              if (response.data.data.list[i].payTime) {
                response.data.data.list[i].payTime = that.gettime(
                  response.data.data.list[i].payTime
                );
              }
            }
            that.tableData = response.data.data.list;
            that.total = response.data.data.totalCount;
          }
        });
    },
    getPrice() {
      let that = this;
      this.axios
        .get(
          // process.env.API_HOST+"expressPrice/my",
          "/giftmall/expressPrice/my",
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              // "domain": "taobao1.tianyalei.com",
              domain: this.url,
              Authorization: sessionStorage.getItem("token")
            }
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
            that.priceForm = response.data.data;
            sessionStorage.setItem(
              "priceForm",
              JSON.stringify(response.data.data)
            );
          }
        });
    },
    capitalDetail() {
      this.$router.push({
        path: "/capitalDetail"
      });
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.size = val;
      this.taskList();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.page = val;
      this.taskList();
    },
    gettime(t) {
      var _time = new Date(t);
      var year = _time.getFullYear(); //2017
      var month =
        _time.getMonth() > 8
          ? _time.getMonth() + 1
          : "0" + (_time.getMonth() + 1); //7
      var date = _time.getDate() > 9 ? _time.getDate() : "0" + _time.getDate(); //10
      var hour =
        _time.getHours() > 9 ? _time.getHours() : "0" + _time.getHours(); //10
      var minute =
        _time.getMinutes() > 9 ? _time.getMinutes() : "0" + _time.getMinutes(); //56
      var second =
        _time.getSeconds() > 9 ? _time.getSeconds() : "0" + _time.getSeconds(); //15

      return (
        year +
        "-" +
        month +
        "-" +
        date +
        "   " +
        hour +
        ":" +
        minute +
        ":" +
        second
      ); //这里自己按自己需要的格式拼接
    }
  }
};
</script>

<style scoped>
.userTop {
  padding: 10px 15px;
  color: #666;
}
.userTop img {
  float: left;
  margin-right: 15px;
}
.fontBt .el-button {
  padding: 0px;
}
.ptkd {
  line-height: 30px;
  height: 210px;
}
.fwzn {
  font-size: 16px;
  font-weight: bold;
}
.fwzn2 {
  line-height: 20px;
  color: #666;
  font-size: 12px;
}
</style>



